import React from 'react';
import { Swiper, SwiperSlide } from "swiper/react";
import "swiper/css";
import "swiper/css/navigation";
import { Navigation } from "swiper";
import img1 from '../../assets/servicios-1.jpg';
import img2 from '../../assets/servicios-2.jpg';
import img3 from '../../assets/servicios-3.jpg';
import img4 from '../../assets/servicios-4.jpg';
import img5 from '../../assets/servicios-5.jpg';
import img6 from '../../assets/servicios-6.jpg';
import "./Servicios.css";

const Servicios =()=>{

    return(
    <section className='servicios__section' id="servicios">
      <div className="slider__container-tittle">
        <h1 className="slider__title">Qu ofrecemos?</h1>
      </div>
      <Swiper navigation={true} modules={[Navigation]}>

          <SwiperSlide className="slider__body">
            <figure>
              <img src={img1} alt="Cardiloga"/>
            </figure>
            <div className="slider__info">
              <h2 className="subtitle">Cardiologa</h2>
              <p className="slider__review">
                Los exmenes cardiolgicos peridicos se han vuelto una prctica necesaria permitiendo la identificacin y tratamiento de enfermedades cardiacas.
              </p>
              <div>
                <h3 className="slider__price">s/100</h3>
                
              </div>
            </div>            
          </SwiperSlide>

          <SwiperSlide className="slider__body">
            <figure>
              <img src={img2} alt="Laboratorista"/>
            </figure>
            <div className="slider__info">
              <h2 className="subtitle">Laboratorio Clnico</h2>
              <p className="slider__review">
                Los exmenes de laboratorio son fundamentales para el diagnstico y tratamiento adecuados de nuestros pacientes.
              </p>
              <div>
                <h3 className="slider__price">s/120</h3>
                
              </div>
            </div>
          </SwiperSlide>

          <SwiperSlide className="slider__body">
            <figure>
              <img src={img3} alt="Anestesia"/>
            </figure>
            <div className="slider__info">
              <h2 className="subtitle">Anestesia</h2>
              <p className="slider__review">
                Pensando en la seguridad del paciente, antes de inducirlo a la anestesia, debemos evaluarlo fsicamente, as como a travs de exmenes preoperatorios.
              </p>
              <div>
                <h3 className="slider__price">s/30</h3>
                
              </div>
            </div>
          </SwiperSlide>

          <SwiperSlide className="slider__body">
            <figure>
              <img src={img4} alt="Microchip de identificacin"/>
            </figure>
            <div className="slider__info">
              <h2 className="subtitle">Microchip de identificacin</h2>
              <p className="slider__review">
                Un dispositivo del tamao de un grano de arroz que se implanta en tu fiel amigo y que le brinda un cdigo de identificacin nico en el mundo. Esta tecnologa ayuda a que cientos de mascotas puedan regresar a sus hogares en caso de prdida o extravo.
              </p>
              <div>
                <h3 className="slider__price">s/90</h3>
                
              </div>
            </div>
          </SwiperSlide>

          <SwiperSlide className="slider__body">
            <figure>
              <img src={img5} alt="Electrocardiograma"/>
            </figure>
            <div className="slider__info">
              <h2 className="subtitle">Electrocardiograma (ECG)</h2>
              <p className="slider__review">
                El ECG es una herramienta para evaluar la actividad elctrica del corazn.
              </p>
              <div>
                <h3 className="slider__price">s/165</h3>
                
              </div>
            </div>
          </SwiperSlide>

          <SwiperSlide className="slider__body">
            <figure>
              <img src={img6} alt="Bistur"/>
            </figure>
            <div className="slider__info">
              <h2 className="subtitle">Ciruga General y Especializada</h2>
              <p className="slider__review">
                Nuestros profesionales se encuentran capacitados para realizar todo tipo de cirugas generales, desde esterilizaciones, hasta extraccin de tumores y especializadas especializadas, desde cirugas plsticas hasta neurocirugas.
              </p>
              <div>
                <h3 className="slider__price">s/150</h3>
                
              </div>
            </div>
          </SwiperSlide>

      </Swiper>
    </section>
  )
}

export {Servicios};